import styled from "styled-components";
import { BoxContentCenter } from "../Box";

export const Button = styled(BoxContentCenter).attrs({
  as: "button",
})`
  position: relative;
  overflow: hidden;
  z-index: 1;

  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgb(${(props) => props.theme.color.ultramarineBlue});

  transition: background-color 150ms ease-in;
  cursor: pointer;

  &:hover {
    background-color: rgb(${(props) => props.theme.color.blueberry});
  }

  &::after {
    position: absolute;
    z-index: -1;

    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgb(${(props) => props.theme.color.cornflowerBlue});

    transform: scale(0);
    transition: transform 150ms ease-in;
    content: "";
  }

  &:active::after {
    transform: scale(1);
  }
`;
