import React from "react";
import styled from "styled-components";
import { Search } from "@styled-icons/material-sharp/Search";

import { Icon } from "../../0-base/Icon";
import { BoxContentLeft } from "../../0-base/Box";

const StyledContainer = styled(BoxContentLeft)`
  position: relative;

  border-radius: 4px;
  background-color: rgba(${(props) => props.theme.color.blueberry});

  &:hover {
    background-color: rgba(${(props) => props.theme.color.cornflowerBlue});
  }
`;

const StyledSearchInput = styled.input.attrs({
  placeholder: "Search...",
})`
  position: relative;

  width: 141px;
  height: 16px;
  padding: 8px 8px 8px 32px;

  color: rgb(${(props) => props.theme.color.cultured});

  transition: width 150ms ease-in;

  &::placeholder {
    color: rgb(${(props) => props.theme.color.jordyBlue});
  }

  &:focus {
    width: 225px;
  }
`;

const StyledSearchIcon = styled(Icon).attrs({
  as: Search,
})`
  position: absolute;
  left: 16px;

  transform: translateX(-50%);
`;

export default function SearchInput(): JSX.Element {
  return (
    <StyledContainer>
      <StyledSearchIcon />
      <StyledSearchInput />
    </StyledContainer>
  );
}
